import React from 'react'
import '../Styles/styles.css'
import Header from '../Header/Header'
import desktopImage from '../Media/calavera.jpg'
import mobileImage from '../Media/descarga.jpg'
import { Link } from 'react-router-dom'

export default function index() {

    const imageUrl = window.innerWidth >= 650 ? desktopImage : mobileImage;

    return (
        <div className="App" style={{ backgroundImage: `url(${imageUrl})` }}>
            <Header />
            <div className="container">
                <div className="row mt-5">
                    <div className="col-md-4" style={{ color: 'white', fontStyle: 'italic' }}>
                        <h2>PUNQU</h2>
                        <p style={{ fontSize: '1.2em' }}>¿Te gusta pasar tiempo en redes sociales? Gana dinero compartiendo imagenes, videos, noticias y experiencias de una manera rapida y sencilla.</p>
                        <p style={{ fontSize: '1.2em' }}>Punqu es una plataforma abierta para personas que les gusta navegar en redes sociales y compartir contenido desde la comodidad de su movil o computador. Registrate y empieza a ganar dinero compartiendo contenido en REDES SOCIALES.</p>
                        <div className="text-center">
                            <Link type="button" className="btn btn-danger" to={"/makemoney/login"}>Empezar Ahora!</Link>
                        </div>
                    </div>
                    <div className="col-md-4"></div>
                    <div className="col-md-4"></div>
                </div>
            </div>
        </div>
    )
}
